/* Links */
a {
  @include type($myriad-pro, $font-weight-regular);
  color: $black;
  text-decoration: underline;
  outline: 0;

  svg {
    height: 22px;
    width: 22px;
    vertical-align: middle;
  }
}

*[class*="__page-content"] p {
 a {
   &:hover,
   &:focus {
     background-color: $hoverPurple;
   }
 }
}

.ama__link--no-underline,
%ama__link--no-underline {
  @include type($myriad-pro, $font-weight-regular);
  text-decoration: none;

  &:link,
  &:visited {
    color: $black;
  }
  &:active,
  &:hover,
  &:focus {
    color: $black;
    text-decoration: underline;
    background-color: transparent;
  }
}
//
.ama__link--white,
%ama__link--white {
  @include type($myriad-pro, $font-weight-regular);
  text-decoration: none;

  &:link,
  &:visited {
    color: $white;
  }
  &:active,
  &:hover,
  &:focus {
    color: $white;
    text-decoration: underline;
    background-color: transparent;
  }
}

.ama__link--blue,
%ama__link--blue {
  @include type($myriad-pro, $font-weight-regular);
  text-decoration: none;

  &:link,
  &:visited {
    color: $blue;
  }
  // if the default link style changes this will need to include color: black; text-decoration: none;
  &:active,
  &:hover,
  &:focus {
    color: $hoverBlue;
    text-decoration: underline;
    background-color: transparent;
  }
}

